const Pet = class {
  constructor() {
    this.name = '';
    this.age = null;
    this.breed = {
      name: '',
      size: '',
    };
    this.id = '';
    this.service = {
      type: '',
      name: '',
      price: 0,
      notes: '',
      finished: false,
      extra: false,
      date: '',
    };
  }
};

export default Pet;
