<template>
  <div class="reception">
    <v-form @submit.prevent="newClient" class="form">
      <h1 class="mb-3">Welcome</h1>
      <v-btn
        @click="petType = 'dog'"
        :class="{ 'teal accent-2': isActive }"
        class="mr-3"
        >Dog</v-btn
      >
      <v-btn @click="petType = 'cat'" :class="{ 'teal accent-2': !isActive }"
        >Cat</v-btn
      >
      <v-text-field
        v-model="client.name"
        name="name"
        label="Client's name"
        id="id"
      ></v-text-field>
      <v-autocomplete
        :items="breeds"
        :filter="customFilter"
        v-model="client.breed"
        item-text="name"
        item-value="name"
        return-object
        label="Breed"
      ></v-autocomplete>
      <v-text-field
        v-model="client.age"
        :rules="[rules.canineAge]"
        type="number"
        name="age"
        label="Age"
        id="age"
      ></v-text-field>
      <v-autocomplete
        :items="services"
        :filter="customFilter"
        v-model="client.service"
        item-text="name"
        item-value="name"
        return-object
        label="Service"
      >
        <template v-slot:item="data"
          >{{ data.item.name }} ({{ data.item.price | price }})</template
        >
      </v-autocomplete>
      <v-textarea
        label="Symptons"
        name="symptons"
        rows="3"
        v-if="client.service && client.service.type === 'appointment'"
        v-model="client.service.notes"
      ></v-textarea>
      <v-btn
        color="success"
        type="submit"
        :disabled="
          client.age >= 30 ||
            client.age < 0 ||
            client.breed.length === 0 ||
            client.service.type.length === 0 ||
            client.name.length === 0
        "
        class="my-2"
        >Add</v-btn
      >
    </v-form>
  </div>
</template>

<script>
import Pet from '../../models/pet';

export default {
  data() {
    return {
      client: new Pet(),
      breeds: this.$store.state.petshop.breeds.dogs,
      services: this.$store.state.petshop.services,
      lodash: require('lodash'),
      rules: {
        canineAge: (value) => value < 30 || 'Are you sure about this age?',
      },
      petType: 'dog',
      isActive: true,
    };
  },

  watch: {
    petType: function(newType) {
      if (newType === 'cat') {
        this.breeds = this.$store.state.petshop.breeds.cats;
      } else {
        this.breeds = this.$store.state.petshop.breeds.dogs;
      }
      this.isActive = !this.isActive;
    },
  },

  methods: {
    newClient() {
      this.client.id = this.lodash.uniqueId();
      const setClient = JSON.parse(JSON.stringify(this.client));
      this.$store.dispatch('newClient', setClient);
      this.client = new Pet();
      this.services[2].notes = '';
    },

    customFilter(item, queryText) {
      const textOne = item.name.toLowerCase();
      const searchText = queryText.toLowerCase();

      return textOne.indexOf(searchText) > -1;
    },
  },
};
</script>

<style lang="scss" scoped>
.reception {
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  padding-bottom: 15px;
  padding-top: 15px;
}

.form {
  width: 80%;
}
</style>
