<template>
  <div>
    <h3>Services of the day</h3>
    <v-simple-table v-if="clients.length > 0" class="text-left">
      <thead>
        <tr>
          <th>Name</th>
          <th>Age</th>
          <th>Breed</th>
          <th>Service</th>
          <th>Value</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(client, index) in clients" :key="index">
          <td>{{ client.name }}</td>
          <td>{{ client.age }}</td>
          <td>{{ client.breed.name }}</td>
          <td>{{ client.service.name }}</td>
          <td>{{ client.service.price | price }}</td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td colspan="4">Total</td>
          <td>{{ servicesTotal | price }}</td>
        </tr>
      </tfoot>
    </v-simple-table>
    <p v-else>No service done today.</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      clients: this.$store.state.petshop.servedClients,
    };
  },

  computed: {
    servicesTotal() {
      if (!Array.isArray(this.clients)) {
        return 0;
      }

      return this.clients.reduce(
        (total, client) => total + client.service.price,
        0
      );
    },
  },
};
</script>

