<template>
  <div class="pt-2 pb-1">
    <h3>Appointment queue</h3>
    <v-simple-table v-if="clients.length > 0" class="text-left">
      <thead>
        <tr>
          <th>Name</th>
          <th>Age</th>
          <th>Breed</th>
          <th>Symptons</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(client, index) in clients" :key="index">
          <td>{{ client.name }}</td>
          <td>{{ client.age }}</td>
          <td>{{ client.breed.name }}</td>
          <td>{{ client.service.notes }}</td>
          <td>
            <v-btn
              elevation="2"
              color="primary"
              x-small
              v-on:click="finishService(client)"
              >Done</v-btn
            >
          </td>
        </tr>
      </tbody>
    </v-simple-table>
    <p v-else>No client waiting.</p>
  </div>
</template>

<script>
import Services from './Services';

export default {
  extends: Services,

  data() {
    return {
      clients: this.$store.state.petshop.vetClients,
    };
  },
};
</script>

